@charset "utf-8";

*, *::after, *::before {
  box-sizing: border-box;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

/* 
minmax(400px, 1fr)
 : 각 셀을 400px 이하로 줄어들지 않게 하며, 400px이상일 경우, 그리드 컨테이너의 너비에 맞게 비율로 그 너비를 정함.
*/

@media screen and (max-width: 1090px) {
  .movie {
    grid-template-columns: 1fr;
    width: 100%;
  }
}
